<template>
    <div class="vis">
        <div class="vis-header">
            <h3>Percepción de la corrupción</h3>
            <p>Uruguay, Chile y Costa Rica se sitúan en una buena posición a nivel mundial por los avances
                institucionales y legales que han realizado en las últimas décadas para fortalecer su gobernanza y
                control de la corrupción. </p>
        </div>
        <div class="vis-legend">
            <svg>
                <defs>
                    <linearGradient id="gradiente-escala" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" style="stop-color:RGB(222,77,75);stop-opacity:1"></stop>
                        <stop offset="35%" style="stop-color:RGB(240,181,98);stop-opacity:1"></stop>
                        <stop offset="70%" style="stop-color:RGB(82,173,146);stop-opacity:1"></stop>
                        <stop offset="100%" style="stop-color:RGB(102,56,240);stop-opacity:1"></stop>
                    </linearGradient>
                </defs>
                <g>
                    <rect id="escala" x="10" y="30" width="0" height="10" fill="url(#gradiente-escala)"></rect>
                </g>
            </svg>
        </div>
        <div class="vis-content">
            <svg></svg>
        </div>
        <div class="vis-footer">
                <p>
                    <img class="footer-image" src="images/logo_bid.svg" />
                    &nbsp;
                    Fuente: Transparencia Internacional (2017)
                </p>
        </div>
    </div>
</template>

<script>
    /* eslint-disable no-unused-vars,no-console */

    import * as d3 from 'd3';
    import * as topojson from 'topojson';

    export default {
        name: 'Visualization',
        props: {
            containerWidth: Number
        },
        data: function () {
            return {
                height: 0,
                width: 0,
                tooltip: undefined,
                tooltipOffset: undefined,
                centered: undefined,
                path: undefined
            }
        },
        mounted: function () {
            console.log("Visualization mounted");
            this.loadMap();
        },
        updated: function () {
            console.log("Visualization updated");
            this.loadMap();
        },
        methods: {
            loadMap: function () {
                console.log("Loading Map");
                this.width = this.containerWidth;
                this.height = this.width * 1.1;

                d3.select("#escala").attr("width", this.width - 20 + "px");

                let projection = d3.geoEquirectangular()
                    .scale(1)
                    .translate([this.width / 2, this.height / 2]);

                this.path = d3.geoPath().projection(projection);

                let svgLegend = d3.select("div.vis-legend > svg")
                    .attr("width", this.width)
                    .attr("height", 65);
                let axisScale = d3.scaleLinear().domain([0, 100]).range([10, this.width - 10]);
                let axis = d3.axisTop(axisScale).tickValues([0, 20, 40, 60, 80, 100]);
                svgLegend.append("g").attr("transform", "translate(0,25)").call(axis);
                svgLegend.append("text")
                    .attr("x", 10)
                    .attr("y", 60)
                    .text("Peor");

                svgLegend.append("text")
                    .attr("x", this.width - 10)
                    .attr("y", 60)
                    .attr("text-anchor", "end")
                    .text("Mejor");


                let svg = d3.select("div.vis-content >svg")
                    .attr("width", this.width)
                    .attr("height", this.height);

                svg.append("g").attr("class", "features").attr("transform", "translate(0,0)");

                let color = d3.scaleLinear()
                    .domain([100, 70, 35, 0])
                    .range(["#6638F0", "#52AD92", "#F0B562", "#DE4D4B"]);

                this.tooltip = d3.select("body").append("div").attr("class", "tooltip");

                let self = this;

                d3.json(process.env.BASE_URL + "data/alc_cpi.topojson").then(function (geodata) {

                    let featureCollection = topojson.feature(geodata, geodata.objects.collection);
                    let scaleCenter = self.calculateScaleCenter(featureCollection);
                    projection.scale(scaleCenter.scale).center(scaleCenter.center);

                    d3.select("g.features").selectAll("path")
                        .data(featureCollection.features)
                        .enter()
                        .append("path")
                        .attr("class", "country")
                        .attr("d", self.path)
                        .attr("style", function (d) {
                            return "fill:" + (typeof color(d.properties.CPI) === "string" ? color(d.properties.CPI) : "");
                        })
                        .on("mouseover", self.showTooltip)
                        .on("mousemove", self.moveTooltip)
                        .on("mouseout", self.hideTooltip)
                        .on("click", self.clicked);

                });

                this.tooltipOffset = {x: 5, y: -35};
            },
            moveTooltip: function () {
                this.tooltip
                    .style("top", (d3.event.pageY + this.tooltipOffset.y) + "px")
                    .style("left", (d3.event.pageX + this.tooltipOffset.x) + "px");
            },
            showTooltip: function (d) {
                this.moveTooltip();
                this.tooltip.style("display", "block").text(d.properties.COUNTRY_ES + " " + d.properties.CPI);
            },
            hideTooltip: function () {
                this.tooltip.style("display", "none");
            },
            clicked: function (d, i) {
                let self = this;
                let x, y, k;
                if (d && this.centered !== d) {
                    let centroid = this.path.centroid(d);
                    let b = this.path.bounds(d);
                    x = centroid[0];
                    y = centroid[1];
                    k = .8 / Math.max((b[1][0] - b[0][0]) / this.width, (b[1][1] - b[0][1]) / this.height);
                    this.centered = d
                } else {
                    x = this.width / 2;
                    y = this.height / 2;
                    k = 1;
                    this.centered = null;
                }

                d3.select("g.features").selectAll("path")
                    .classed("highlighted", function (d) {
                        return d === self.centered;
                    })
                    .style("stroke-width", 0.5 / k + "px");

                d3.select("g.features")
                    .transition()
                    .duration(300)
                    .attr("transform", "translate(" + this.width / 2 + "," + this.height / 2 + ")scale(" + k + ")translate(" + -x + "," + -y + ")");
            },
            calculateScaleCenter: function (features) {

                let bbox_path = this.path.bounds(features),
                    scale = 1 / Math.max(
                        (bbox_path[1][0] - bbox_path[0][0]) / this.width,
                        (bbox_path[1][1] - bbox_path[0][1]) / this.height
                    );

                let bbox_feature = d3.geoBounds(features),
                    center = [
                        (bbox_feature[1][0] + bbox_feature[0][0]) / 2,
                        (bbox_feature[1][1] + bbox_feature[0][1]) / 2];

                return {
                    'scale': scale,
                    'center': center
                };
            }
        }
    }
</script>

