    div.tooltip {
        position: absolute;
        background-color: white;
        border: 1px solid black;
        color: black;
        font-weight: bold;
        padding: 4px 8px;
        display: none;
    }

    div.vis {
        padding-top: 10px;
        margin: 0 auto;
    }

    div.vis-header {
